.map {
	position: relative;
	height: 600px;
	overflow: hidden;
	padding: 0;
	@media screen and (max-width: 840px) {
		display: flex;
		flex-direction: column;
		height: auto;
		background-color: $main-color; }
	iframe {
		position: relative;
		z-index: 1;
		@media screen and (max-width: 840px) {
			position: static;
			height: 300px;
			z-index: 0; } }
	.container {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		margin: 0 auto;
		@media screen and (max-width: 840px) {
			order: -1;
			position: static;
			margin: auto; } }
	// .map__inner
	&__inner {
		width: 100%;
		height: 100%; }

	// .map__content
	&__content {
		position: relative;
		width: 45%;
		height: 100%;
		z-index: 1;
		@media screen and (max-width: 840px) {
			display: flex;
			flex-direction: column;
			align-items: center;
			width: 100%; }
		&::before {
			content: "";
			position: absolute;
			display: block;
			top: 50%;
			left: 0;
			width: 1574px;
			height: 1574px;
			border-radius: 50%;
			background-color: $main-color;
			transform: translate(-950px, -50%);
			z-index: -1;
			@media screen and (max-width: 1100px) {
				transform: translate(-1050px, -50%); }
			@media screen and (max-width: 840px) {
				display: none; } } }

	// .map__title
	&__title {
		margin-top: 7.5rem;
		margin-bottom: 7.5rem;
		color: $second-color;
		text-align: left;
		@media screen and (max-width: 840px) {
			text-align: center; } }

	// .map__contact-details
	&__contact-details {
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		@media screen and (max-width: 840px) {
			align-items: flex-start;
			width: auto;
			flex: 1 1 auto; } }

	// .map__contact-details__link
	&__contact-details__link {
		margin-bottom: 5rem;
		font-size: 2rem;
		font-weight: 500;
		line-height: 150%; }

	// .map__contact-details__image
	&__contact-details__image {
		margin-right: 20px; } }

.title {}

.contact-details {
	// .contact-details__link
	&__link {}

	// .contact-details__image
	&__image {} }

