.testimonials {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	align-items: stretch;
	padding: 0;
	overflow: hidden;
	// .testimonials__image
	&__image {
		flex: 0 0 55%;
		font-size: 0;
		@media screen and (max-width: 940px) {
			flex-basis: 50%; }
		@media screen and (max-width: 683px) {
			display: none; } }

	// .testimonials__bg
	&__bg {
		width: 100%;
		height: 100%;
		object-fit: cover; }

	// .testimonials__content
	&__content {
		flex: 0 0 45%;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		padding: 7rem 10% 10rem 6rem;
		background-color: darken($blue, 1%);
		box-shadow: -30px -40px 70px darken($blue, 2%), -30px 40px 70px darken($blue, 2%);
		overflow: hidden;
		@media screen and (max-width: 940px) {
			flex-basis: 50%; }
		@media screen and (max-width: 683px) {
			flex-basis: 100%; } }

	// .testimonials__suptitle
	&__suptitle {
		margin-bottom: 2.5rem; }

	// .testimonials__title
	&__title {
		margin-bottom: 5rem;
		text-align: left; }

	// .testimonials__slider
	&__slider {
		width: 100%; }

	// .testimonials__item
	&__item {
		outline: none; }

	// .testimonials__header
	&__header {
		font-weight: 600;
		font-size: 2.4rem;
		color: $second-text-color;
		margin-bottom: 3rem; }

	// .testimonials__text
	&__text {
		margin-bottom: 20px;
		font-style: italic;
		font-weight: 500;
		font-size: 1.6rem;
		color: rgba(#2D2738, 0.8);
		line-height: 170%; }

	// .testimonials__footer
	&__footer {}

	// .testimonials__author
	&__author {
		font-weight: 600;
		font-size: 1.6rem;
		color: $second-text-color; }

	// .testimonials__client
	&__client {
		font-weight: 500;
		font-size: 1.6rem;
		color: rgba(#2D2738, 0.8); }

	.slick-dots {
		left: 0;
		bottom: -75px;
		width: auto;
		li {
			margin: 0 0.5rem; }
		& button {
			background-color: #C4C4C4;
			border-radius: 50%;
			width: 2rem;
			height: 2rem;
			&::before {
				display: none; } }
		& .slick-active button {
			background: #ADC867; } } }



