.footer {
	padding: 7rem 0 3rem;
	color: $second-color;
	background-color: $purple;//$main-color
	// .footer__inner
	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: stretch;
		width: 100%;
		height: 100%;
		@media screen and (max-width: 576px) {
			flex-flow: row wrap;
			align-items: flex-start; }
		@media screen and (max-width: 425px) {
			flex-flow: column nowrap;
			align-items: center; } }

	// .footer__info
	&__info {
		flex: 0 1 25%;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		padding-right: 1.5rem;
		@media screen and (max-width: 768px) {
			flex-basis: 35%; }
		@media screen and (max-width: 576px) {
			flex: 0 0 100%;
			align-items: center;
			padding-right: 0; } }

	// .footer__logo
	&__logo {
		font-weight: 500;
		font-size: 2.4rem;
		color: $second-color;
		margin-bottom: 5rem;
		span {
			font-weight: 500;
			font-size: 2.4rem;
			color: rgba($second-color, 0.5); } }

	// .footer__logo-descr
	&__logo-descr {
		flex: 1 1 auto;
		font-size: 1.4rem;
		font-weight: 500;
		color: rgba($second-color, 0.7);
		line-height: 160%;
		@media screen and (max-width: 576px) {
			display: none; } }

	// .footer__contact
	&__contact {
		flex: 1 1 auto;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		width: 100%;
		@media screen and (max-width: 576px) {
			display: none; } }
	&__contact-link {
		margin-right: 20px;
		&:last-child {
			margin-right: 0; } }

	// .footer__contact-details
	&__contact-details {
		flex: 0 1 30%;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		padding-right: 1.5rem;
		@media screen and (max-width: 768px) {
			flex-basis: 40%; }
		@media screen and (max-width: 576px) {
			flex: 0 0 70%; }
		@media screen and (max-width: 425px) {
			align-items: flex-start;
			padding-right: 0;
			margin-bottom: 5rem; }
		.contact-details__title {
			margin-bottom: 5rem;
			font-weight: 500;
			font-size: 2.4rem;
			@media screen and (max-width: 425px) {
				width: 100%;
				text-align: center; } }
		.contact-details__link {
			font-size: 1.4rem;
			font-weight: 400;
			margin-bottom: 3rem;
			color: rgba($second-color, 0.7);
			&:last-child {
				margin-bottom: 0; }
			&:hover {
				color: $third-color; } } }

	// .footer__menu
	&__menu {
		flex: 0 1 10%;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		padding-right: 1.5rem;
		@media screen and (max-width: 768px) {
			flex-basis: 20%;
			align-items: center;
			padding-right: 0; }
		@media screen and (max-width: 576px) {
			flex: 0 0 30%; } }
	// .footer__menu-title
	&__menu-title {
		margin-bottom: 5rem;
		font-weight: 500;
		font-size: 2.4rem;
		color: $second-color; }

	// .footer__bestdeal
	&__bestdeal {
		flex: 0 1 300px; } }

.footer-nav {
	// .footer-nav__list
	li {
		flex: 0 1 auto;
		width: 100%;
		margin: 0;
		padding: 0;
		&:last-child {
			padding-right: 0; } }

	&__list {
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start; }

	// .footer-nav__link
	&__link {
		margin-bottom: 2.5rem;
		padding: 5px;
		font-size: 1.4rem;
		font-weight: 600;
		color: rgba($second-color, 0.7);
		text-align: left;
		line-height: 140%;
		&.active {
			color: $third-color;
			background: none; }
		&:hover {
			color: $third-color;
			background: none; } } }


.bestdeal {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	@media screen and (max-width: 768px) {
		display: none; }
	// .bestdeal__title
	&__title {
		margin-bottom: 5rem;
		font-weight: 500;
		font-size: 2.4rem;
		color: $second-color;
		white-space: nowrap; }

	// .bestdeal__item
	&__item {
		flex: 0 0 auto;
		margin-bottom: 0;
		padding-left: 0;
		padding-right: 0; }

	// .bestdeal__card
	&__card {}

	.houses__header {
		height: 200px; }


	.houses__type, .houses__for {
		display: inline-block;
		position: absolute;
		width: 42%;
		top: 15px;
		padding: 15px 5px;
		font-size: 1.6rem;
		font-weight: 600;
		color: $second-color;
		text-align: center;
		z-index: 1; } }
