.header-main {
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	display: flex;
	justify-content: flex-start;
	flex-direction: column;
	z-index: 9;
	transition: transform .3s linear;
	box-shadow: 0 1px 20px rgba($main-color, 40%);
	@media screen and (max-width: 576px) {
		top: auto;
		bottom: 0; }
	&.scroll {
		transform: translateY(-50%);
		@media screen and (max-width: 768px) {
			transform: translateY(0); } }
	// .header-main__contacts
	&__contacts {
		display: flex;
		justify-content: space-between;
		align-items: center;
		background-color: $main-color;
		height: 65px;
		padding: 0 15px;
		@media screen and (max-width: 1600px) {
			padding-right: 5px; }
		@media screen and (max-width: 1015px) {
			height: 60px; }
		@media screen and (max-width: 940px) {
			padding: 0 5px; }
		@media screen and (max-width: 768px) {
			display: none; } }

	// .header-main__block
	&__block {
		display: flex;
		justify-content: space-between;
		align-items: center;
		background-color: $second-color;
		height: 65px;
		padding-left: 15px;
		@media screen and (max-width: 1200px) {
			height: 70px; }
		@media screen and (max-width: 1600px) {
			height: 65px; }
		@media screen and (max-width: 1015px) {
			height: 60px; }
		@media screen and (max-width: 940px) {
			padding-left: 5px; }
		@media screen and (max-width: 768px) {
			padding-right: 15px;
			padding-left: 15px;
			box-shadow: 0 1px 20px rgba($main-color, 40%); } }

	// .contact-social__image
	&__image {
		width: 25px;
		height: 25px; } }

.header {
	// .header__logo
	&__logo {
		flex: 1 0 35%;
		padding-right: 10px;
		white-space: nowrap;
		@media screen and (max-width: 1440px) {
			flex-grow: 1; }
		@media screen and (max-width: 940px) {
			flex: 0 1 auto; }
		@media screen and (max-width: 768px) {
			flex-grow: 0; } }

	// .header__nav
	&__nav {
		flex: 1 0 40%;
		height: 100%;
		@media screen and (max-width: 768px) {
			position: fixed;
			top: 60px;
			right: 0;
			min-width: 320px;
			height: calc(100% - 60px);
			background-color: $second-color;
			transform: translateX(100%);
			transition: transform .3s;
			z-index: 8;
			& .nav__list {
				flex-direction: column;
				height: auto;
				padding-top: 30px;
				overflow: auto;
				& li {
					flex: 0 0 auto;
					margin-bottom: 2rem; }
				& .nav__link {
					font-size: 2.5rem;
					background: none;
					padding: 1.5rem;
					&.active {
						color: $third-color; }
					&:hover {
						color: $third-color; } } } }
		@media screen and (max-width: 576px) {
			top: 0;
			bottom: auto; }
		&.active {
			transform: translateX(0); } }

	// .header__details
	&__details {
		justify-content: flex-start;
		padding-right: 20px; }

	// .header__nav-link
	&__nav-link {
		&.active {
			color: $second-color;
			background-color: $third-color; } }

	// .header__feedback-btn
	&__feedback-btn {
		flex: 0 0 calc(12% + 15px);
		@media screen and (max-width: 1600px) {
			flex: 0 0 205px; }
		@media screen and (max-width: 840px) {
			flex: 1 0 auto; }
		@media screen and (max-width: 768px) {
			display: none; } } }

