.services {
	background-color: darken($blue, 1%);
	.services__inner {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: center; }

	.services__suptitle {}

	.services__title {}

	.services__cards {
		display: flex;
		flex-flow: row wrap;
		align-items: stretch;
		justify-content: flex-start;
		margin: 0 -20px -20px;
		@media screen and (max-width: 576px) {
			margin: 0 -10px -10px; } }

	.services__item {
		flex: 0 1 33.33333%;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		padding: 0 20px 40px;
		@media screen and (max-width: 840px) {
			flex-basis: 50%; }
		@media screen and (max-width: 576px) {
			padding: 0 10px 20px; }
		@media screen and (max-width: 527px) {
			flex-basis: 100%; } }

	.services__item-inner {
		height: 100%;
		padding: 35px 30px;
		border: 2px solid transparent;
		background-color: $second-color;
		transition: border .3s linear, box-shadow .3s linear;
		&:hover {
			border: 2px solid $third-color;
			box-shadow: 0px 0px 25px darken($second-color, 18%); } }


	.services__item-header {
		width: 100%;
		height: 40%;
		display: flex;
		flex-flow: row nowrap;
		justify-content: flex-start;
		align-items: center;
		margin-bottom: 2.5rem; }

	.services__item-image {
		flex: 0 0 38px;
		height: 38px;
		margin-right: 15px;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }

	.services__item-title {
		font-weight: 600;
		font-size: 2.4rem;
		color: $second-text-color;
		//text-align: center
 }		//white-space: nowrap

	.services__item-text {
		width: 100%;
		font-size: 1.4rem;
		font-weight: 500;
		color: rgba(#2D2738, 0.7);
		line-height: 170%; } }


