.about {
	// .about__inner
	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: stretch; }
	// .about__content
	&__content {
		flex: 1 1 50%;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		margin-right: 3.5rem;
		@media screen and (max-width: 576px) {
			align-items: center; } }

	// .about__suptitle
	&__suptitle {
		text-align: left; }

	// .about__title
	&__title {
		text-align: left;
		margin-bottom: 4rem; }

	// .about__text
	&__text {
		font-size: 1.6rem;
		font-weight: 400;
		color: rgba(#2D2738, 0.8);
		line-height: 170%; }

	// .about__image
	&__image {
		flex: 1 1 50%;
		@media screen and (max-width: 576px) {
			display: none; }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } } }
