.parallax-block {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 26rem;
	padding-top: 130px;
	overflow: hidden;
	@media screen and (max-width: 768px) {
		padding-top: 60px;
		height: 20rem; }
	@media screen and (max-width: 576px) {
		height: auto;
		padding: 20px 5px; }
	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba($main-color, 0.75);
		z-index: 1; }
	// .parallax-block__image
	&__image {
		position: absolute;
		top: -70%;
		width: 100%;
		height: auto; }
	// .parallax-block__title
	&__title {
		position: relative;
		font-weight: 600;
		font-size: 3rem;
		color: $second-color;
		text-align: center;
		z-index: 2; } }


