@import "fonts";
@import "vars";
@import "mixins";

*, *:before, *:after {
	box-sizing: border-box; }

h1,h2,h3,h4,h5,h6 {
	margin: 0; }

p {
	margin: 0; }

ul {
	list-style-type: none;
	margin: 0;
	padding: 0; }

a {
	color: $second-color;
	text-decoration: none; }

html {
	width: 100%;
	height: 100%;
	font-family: $main-ff;
	font-size: 10px;
	font-weight: 400;
	@media screen and (max-width: 1100px) {
		font-size: 9px; }
	@media screen and (max-width: 1015px) {
		font-size: 8px; }
	@media screen and (max-width: 940px) {
		font-size: 7px; } }

body {
	width: 100%;
	height: 100%;
	font-size: 1.6rem;
	color: $second-text-color;
	background-color: $main-color;
	overflow-x: hidden; }

.wrapper {
	width: 100%;
	height: 100%; }

.container {
	max-width: 1200px;
	width: 100%;
	height: 100%;
	margin: 0 auto;
	padding: 0 15px; }

section {
	padding: 7rem 0;
	background-color: $second-color; }

.logo {
	font-size: 3.2rem;
	font-weight: 600;
	color: #2D2738;
	span {
		font-weight: 400;
		color: lighten(#2D2738, 40%); }
	@media screen and (max-width: 1200px) {
		font-size: 2.8rem; }
	@media screen and (max-width: 476px) {
		font-size: 2.7rem; } }

.title {
	margin-bottom: 6.5rem;
	font-size: 4.6rem;
	font-weight: 600;
	text-align: center;
	color: $second-text-color; }

.suptitle {
	margin-bottom: 2.5rem;
	font-size: 1.4rem;
	font-weight: 600;
	text-transform: uppercase;
	text-align: center;
	letter-spacing: 2px;
	color: rgba(32, 16, 62, 0.5); }

.nav {
	// .nav__list
	&__list {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		height: 100%;
		li {
			flex: 1 1 110px;
			height: 100%;
			margin-right: 20px;
			padding-right: 5px;
			&:last-child {
				margin-right: 0;
				padding-right: 20px;
				@media screen and (max-width: 1440px) {
					padding-right: 0; } }
			@media screen and (max-width: 1200px) {
				margin-right: 0;
				padding-right: 0; } } }
	// .nav__link
	&__link {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 100%;
		font-weight: 600;
		font-size: 1.6rem;
		color: #2D2738;
		text-align: center;
		transition: color .1s linear, background-color .2s linear;
		&:hover {
			color: $second-color;
			background-color: $third-color; } } }

.contact-details {
	flex: 1 0 40%;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	@media screen and (max-width: 1440px) {
		flex: 1 1 40%; }

	// .contact-details__image
	&__image {
		width: 25px;
		height: 25px;
		margin-right: 15px;
		cursor: pointer;
		pointer-events: none;
		fill: $second-color;
		@media screen and (max-width: 1440px) {
			margin-right: 10px; }
		@media screen and (max-width: 840px) {
			margin-right: 5px;
			width: 22px;
			height: 22px; } }

	// .contact-details__link
	&__link {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		margin-right: 45px;
		padding: 5px;
		font-size: 1.6rem;
		color: $second-color;
		line-height: 160%;
		transition: color .2s linear;
		&:hover {
			color: $third-color; }
		&:last-child {
			margin-right: 0; }
		@media screen and (max-width: 1200px) {
			margin-right: 10px; }
		@media screen and (max-width: 1100px) {
			margin-right: 8px;
			padding: 0; }
		@media screen and (max-width: 840px) {
			font-size: 1.5rem; } } }


.contact-social {
	flex: 0 0 12%;
	display: flex;
	justify-content: space-between;
	@media screen and (max-width: 1600px) {
		flex: 0 0 200px; }
	@media screen and (max-width: 840px) {
		flex-basis: 180px; }
	// .contact-social__link
	&__link {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 42px;
		height: 42px;
		background-color: $third-color;
		border-radius: 50%;
		transition: background-color .2s linear;
		&:hover {
			background-color: darken($third-color, 17%); }
		@media screen and (max-width: 940px) {
			width: 38px;
			height: 38px; } } }



