.contacts {
	// .contacts__inner
	&__inner {}

	// .contacts__suptitle
	&__suptitle {}

	// .contacts__title
	&__title {
		margin-bottom: 9rem; }

	// .contacts__info
	&__info {
		display: flex;
		flex-flow: row nowrap;
		justify-content: space-between;
		align-items: stretch;
		margin-bottom: 10rem;
		@media screen and (max-width: 768px) {
			margin-bottom: 5rem; } }

	// .contact__descr
	&__descr {
		flex: 0 1 33.33333%;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		padding-right: 3rem;
		@media screen and (max-width: 768px) {
			flex-basis: 40%; }
		& .logo {
			font-weight: 500;
			font-size: 2.4rem;
			margin-bottom: 3.5rem;
			color: $second-text-color;
			span {
				font-weight: 500;
				font-size: 2.4rem;
				color: rgba($second-text-color, 0.5); } }
		& .logo__descr {
			font-size: 1.6rem;
			font-weight: 400;
			color: $second-text-color; } }

	// .contact__details
	&__details {
		flex: 1;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		padding-right: 3rem;
		@media screen and (max-width: 768px) {
			padding-right: 0; }
		.contact-details__link {
			margin-right: 0;
			margin-bottom: 1.5rem;
			.contact-details__image-wrap {
				flex-shrink: 0;
				display: flex;
				justify-content: center;
				align-items: center;
				margin-right: 15px;
				width: 40px;
				height: 40px;
				padding: 9px;
				border-radius: 50%;
				background-color: $third-color;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					margin: 0; } }
			span {
				font-size: 1.6rem;
				font-weight: 500;
				color: $second-text-color;
				transition: color 0.3s; }
			&:hover span {
				color: $third-color; } } }

	// .contact__img
	&__img {
		margin-top: -25px;
		@media screen and (max-width: 768px) {
			display: none; }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }

	// .contact__map
	&__map {
		height: 600px;
		@media screen and (max-width: 768px) {
			height: 300px; }
		&::after {
			content: '';
			display: none;
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: transparent;
			z-index: 1; } } }


.contacts-form {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	width: 100%;

	&__header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		margin-bottom: 35px;
		@media screen and (max-width: 768px) {
			flex-direction: column;
			margin-bottom: 1.5rem; } }

	&__input {
		flex: 1;
		margin-right: 40px;
		padding: 20px;
		border: 2px solid transparent;
		background-color: $blue;
		&:last-child {
			margin-right: 0; }
		@media screen and (max-width: 768px) {
			width: 100%;
			margin-right: 0;
			margin-bottom: 1.5rem; } }

	&__message {
		width: 100%;
		min-height: 220px;
		resize: none;
		padding: 20px;
		margin-bottom: 40px;
		border: 2px solid transparent;
		background-color: $blue; }

	&__submit {
		font-size: 1.6rem;
		font-weight: 700;
		min-width: 250px;
		padding: 25px;
		color: $second-color;
		text-transform: capitalize;
		background-color: $third-color; } }

