.houses {
	display: flex;
	flex-flow: row wrap;
	justify-content: flex-start;
	margin: 0 -15px;

	// .houses__item
	&__item {
		flex: 0 1 33.33333%;
		display: flex;
		flex-direction: column;
		padding: 0 15px;
		margin-bottom: 50px;
		@media screen and (max-width: 840px) {
			flex-basis: 50%; }
		@media screen and (max-width: 527px) {
			flex-basis: 100%; } }

	// .houses__card
	&__card {
		flex-grow: 1;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: flex-start; }

	// .houses__header
	&__header {
		position: relative;
		font-size: 0;
		height: 300px;
		overflow: hidden;
		&:hover {
			.houses__image img {
				transform: scale(1.15); } } }

	.houses__type, .houses__for {
		display: inline-block;
		position: absolute;
		width: 42%;
		top: 15px;
		padding: 15px 5px;
		font-size: 1.6rem;
		font-weight: 600;
		color: $second-color;
		text-align: center;
		z-index: 1;
		@media screen and (max-width: 576px) {
			font-size: 1.8rem; }
		@media screen and (max-width: 527px) {
			width: auto;
			min-width: 30%;
			padding: 15px; } }

	// .houses__type
	&__type {
		left: 15px;
		background-color: $third-color; }

	// .houses__for
	&__for {
		right: 15px;
		background-color: #55407D;
		&--rent {
			background-color: $rent; } }

	// .houses__image
	&__image {
		& img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			transition: transform 1s ease-out; } }

	// .houses__info
	&__info {
		flex: 1 1 auto;
		display: flex;
		justify-content: flex-start;
		flex-flow: row nowrap;
		align-items: flex-start;
		position: relative;
		width: 100%;
		padding: 45px 30px 35px;
		background-color: darken($blue, 3%); }

	// .houses__cost
	&__cost {
		display: inline-block;
		position: absolute;
		top: 0;
		left: 30px;
		width: 42%;
		padding: 15px 10px;
		font-size: 1.6rem;
		font-weight: 600;
		text-align: center;
		color: $second-color;
		background-color: $pink;
		transform: translateY(-50%);
		@media screen and (max-width: 576px) {
			font-size: 1.8rem; }
		@media screen and (max-width: 527px) {
			width: auto;
			min-width: 30%;
			padding: 15px; } }

	// .houses__place
	&__place {
		position: relative;
		top: 2px;
		width: 25px;
		height: 25px;
		margin-right: 15px; }

	// .houses__location
	&__location {
		font-size: 1.4rem;
		font-weight: 600;
		color: darken($grey, 15%);
		line-height: 165%;
		@media screen and (max-width: 940px) {
			font-size: 1.5rem; }
		@media screen and (max-width: 576px) {
			font-size: 1.7rem; } } }
