.intro {
	position: relative;
	width: 100%;
	min-height: 100vh;
	height: 100%;
	padding-top: 130px;
	display: flex;
	justify-content: center;
	align-items: center;
	@media screen and (max-width: 768px) {
		padding-top: 60px; }
	@media screen and (max-width: 576px) {
		align-items: flex-start;
		padding-top: 30px;
		padding-bottom: 70px; }

	// .intro__image
	&__image {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		z-index: -1; }

	// .intro__offer
	&__offer {
 }		//margin-bottom: -70px

	// .intro__suptitle
	&__suptitle {
		font-size: 3rem;
		font-weight: 600;
		text-align: center;
		line-height: 4rem;
		color: $second-color; }

	// .intro__title
	&__title {
		font-weight: 700;
		font-size: 3.8rem;
		line-height: 4.6rem;
		color: $second-color; } }

#offer-form {
	position: relative;
	display: flex;
	align-items: stretch;
	justify-content: space-between;
	max-width: 1200px;
	padding: 2rem;
	background-color: rgba($purple, 0.85);
	@media screen and (max-width: 683px) {
		flex-direction: column;
		width: 100%; } }

.offer {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	padding: 0 15px;
	// .offer__suptitle
	&__suptitle {
		flex: 1 0 250px;
		width: 500px;
		padding: 2rem;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: rgba($purple, 0.99);
		@media screen and (max-width: 1015px) {
			max-width: 65%; }
		@media screen and (max-width: 683px) {
			flex-basis: auto;
			width: 100%;
			max-width: none; } }

	// .offer__title
	&__title {
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
		padding: 2.7rem 2rem;
		background-color: $third-color;
		@media screen and (max-width: 1015px) {
			min-width: 65%; }
		@media screen and (max-width: 683px) {
			order: -1;
			width: 100%;
			max-width: none; } }

	// .offer__select
	&__select {
		position: relative;
		flex: 0 0 24%;
		height: 100%;
		&::after {
			content: "";
			position: absolute;
			top: 50%;
			right: 2rem;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 10px 6px 0 6px;
			border-color: rgba(32, 16, 62, 0.9) transparent transparent transparent;
			transform: translateY(-50%);
			pointer-events: none;
			@media screen and (max-width: 768px) {
				right: 1rem; } }
		@media screen and (max-width: 840px) {
			flex-basis: 25%; }
		@media screen and (max-width: 683px) {
			margin-bottom: 1rem; }
		select {
			width: 100%;
			padding: 2rem;
			border: 2px solid transparent;
			font-size: 1.6rem;
			font-weight: 500;
			color: rgba(32, 16, 62, 0.9);
			appearance: none;
			&:focus {
				outline: none;
				border: 2px solid darken($third-color, 25%); }
			@media screen and (max-width: 768px) {
				padding-left: 1.3rem;
				color: rgba(32, 16, 62, 1); } } }

	// .offer__number
	&__number {
		flex: 0 0 20%;
		margin-left: -4%;
		position: relative;
		@media screen and (max-width: 840px) {
			flex: 0 1 24%;
			margin-left: 0; }
		@media screen and (max-width: 683px) {
			margin-bottom: 1rem; }
		label {
			position: absolute;
			left: 2rem;
			top: 50%;
			font-weight: 500;
			font-size: 1.6rem;
			color: rgba(32, 16, 62, 0.9);
			transform: translateY(-50%);
			@media screen and (max-width: 768px) {
				left: 1.4rem;
				color: rgba(32, 16, 62, 1);
				text-transform: capitalize;
				span {
					display: none; } } }
		input {
			width: 100%;
			appearance: none;
			-moz-appearance: textfield;
			border: 2px solid transparent;
			padding: 2rem 1.6rem 2rem 10rem;
			font-weight: 500;
			font-size: 1.7rem;
			color: rgba(32, 16, 62, 0.9);
			&:focus {
				outline: none;
				border: 2px solid darken($third-color, 25%); }
			&::-webkit-outer-spin-button,
			&::-webkit-inner-spin-button {
				-webkit-appearance: none;
				margin: 0; }
			@media screen and (max-width: 768px) {
				padding-left: 7rem;
				color: rgba(32, 16, 62, 1); } } }

	// .offer__from
	&__from {}

	// .offer__to
	&__to {}

	// .offer__submit
	&__submit {
		flex: 0 0 25%;
		font-weight: 600;
		font-size: 1.7rem;
		padding: 2.1rem 2rem;
		background-color: $third-color;
		color: $second-color;
		letter-spacing: 2px;
		transition: background-color .2s linear;
		&:hover {
			background-color: darken($third-color, 17%);
			color: #fff; }
		&:active {
			box-shadow: 0px -1px 4px darken(green, 50%);
			transform: translateY(2px); } } }










