.btn {
	display: inline-block;
	font-size: 1.6rem;
	font-weight: 700;
	text-align: center;
	text-decoration: none;
	text-transform: uppercase;
	letter-spacing: 1px;
	cursor: pointer;
	border: none;
	&:focus {
		outline: none; } }

.menu__btn {
	position: relative;
	display: none;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	width: 20px;
	height: 20px;
	padding: 0;
	border: none;
	font: inherit;
	color: inherit;
	background-color: inherit;
	cursor: pointer;
	&:hover &-item {
		background-color: $third-color; }
	&:focus {
		outline: none; }
	&-item {
		width: 20px;
		height: 4px;
		border-radius: 8px;
		background-color: $second-text-color;
		transition: transform .2s linear, background-color .2s linear; }
	&.active &-item {
		position: absolute; }
	&.active &-item:nth-child(2) {
		display: none; }
	&.active &-item:first-child {
		width: 22px;
		transform: translateY(0px) rotate(45deg); }
	&.active &-item:last-child {
		width: 22px;
		transform: translateY(-0px) rotate(-45deg); }
	@media screen and (max-width: 768px) {
		display: flex; } }

.feedback-btn {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	color: $second-color;
	background-color: $third-color;
	text-transform: capitalize;
	transition: background-color .2s linear;
	&:hover {
		background-color: darken($third-color, 15%);
		color: #fff; }
	&:active {
		box-shadow: 0px 0px 1px darken(green, 50%);
		transform: translateX(1px); } }

.search__btn {
	padding: 2rem;
	font-size: 1.6rem;
	font-weight: 700;
	color: $second-color;
	text-transform: capitalize;
	text-transform: none;
	background-color: $third-color;
	box-shadow: 0px 2px 3px $third-color;
	transition: background-color .2s linear;
	&:hover {
		background-color: darken($third-color, 15%);
		color: #fff; }
	&:active {
		//box-shadow: 0px 0px 0px darken(green, 50%)
		transform: translateY(1px); } }
